import React from 'react';
import { Menu, Dropdown } from 'antd';
import { Link } from 'gatsby';
import { DownOutlined } from '@ant-design/icons';
import classes from '../Nav/Nav.module.scss';

const platform = (
	<Menu>
		<Menu.Item>
			<Link className={`${classes.navItems} pt-3 pb-3 text-center`} to="/blog/game">
				جدیدترین گیم ها
			</Link>
		</Menu.Item>
		<Menu.Item>
			<Link className={`${classes.navItems} pt-3 pb-3 text-center`} to="/blog">
				جدیدترین مطالب
			</Link>
		</Menu.Item>
		<Menu.Item>
			<Link className={`${classes.navItems} pt-3 pb-3 text-center`} to="/blog/mobile">
				گوشی موبایل
			</Link>
		</Menu.Item>
		<Menu.Item>
			<Link className={`${classes.navItems} pt-3 pb-3 text-center`} to="/blog/tech">
				تکنولوژی
			</Link>
		</Menu.Item>
		<Menu.Item>
			<Link className={`${classes.navItems} pt-3 pb-3 text-center`} to="/blog/other">
				سایر مطالب
			</Link>
		</Menu.Item>
	</Menu>
);

function BlogDropdown() {
	return (
		<Dropdown className="ml-3" placement="bottomCenter" trigger={[ 'click' ]} overlay={platform}>
			<span className={`${classes.navItems} ant-dropdown-link`}>
				آخرین مطالب <DownOutlined />
			</span>
		</Dropdown>
	);
}

export default BlogDropdown;
