import React, { useState, createRef } from 'react';
import Media from 'react-media';
import { navigate } from 'gatsby';
import classes from './InputMedia.module.scss';

function InputMedia({ cls, media, clicked, drawer }) {
	const inputRef = createRef();
	const [ input, setInput ] = useState('');
	const handleInput = (e) => {
		setInput(e);
	};
	return (
		<Media
			query={`(${media}-width: 960px)`}
			render={() => (
				<input
					dir="rtl"
					className={`${classes.input} mr-3 ${cls}`}
					onChange={(e) => handleInput(e.target.value)}
					onKeyDown={(e) => {
						if (e.key === 'Enter') {
							navigate(`/search?s=${input}&page=1`);
							inputRef.current.blur();
							if (drawer) {
								clicked();
							}
						}
					}}
					value={input}
					type="text"
					placeholder="جستجو ..."
					ref={inputRef}
				/>
			)}
		/>
	);
}

export default InputMedia;
