import React, { Fragment } from 'react';
import { Link } from 'gatsby';

function FooterRight() {
	return (
		<Fragment>
			<p style={{ color: '#9e9e9e' }} className="text-right m-0 mt-2 mb-2">
				دسته بندی محصولات
			</p>
			<hr className="m-0" />
			<Link to="/category/items" className="text-right d-block mt-2 w-50">
				لوازم جانبی
			</Link>
			<Link to="/category/console" className="text-right d-block mt-2 w-50">
				کنسول گیمینگ
			</Link>
			<Link to="/shop" className="text-right d-block mt-2 w-50">
				همه ی محصولات
			</Link>
		</Fragment>
	);
}

export default FooterRight;
