import React, { useState } from 'react';
import { Link } from 'gatsby';
import { Drawer } from 'antd';
import classes from './Nav.module.scss';

// Logo
import warzone from '../../img/warzone.png';
import cod from '../../img/up.png';
import burger from '../../img/burger.png';

// Components
import DrawerComp from '../DrawerComp/DrawerComp';
import InputMedia from '../InputMedia/InputMedia';
import Platform from '../Platform/Platform';
import BlogDropdown from '../BlogDropdown/BlogDropdown';

function Nav() {
	const [ visible, setVisible ] = useState(false);

	const onClose = () => {
		setVisible(false);
	};

	return (
		<React.Fragment>
			<Drawer
				drawerStyle={{ background: 'white' }}
				onClose={onClose}
				maskClosable={true}
				closable={false}
				placement="left"
				visible={visible}
				className="m-0 p-0"
			>
				<DrawerComp close={onClose} />
			</Drawer>
			<div className={`d-flex justify-content-between align-items-center ${classes.nav}`}>
				<div className="d-flex align-items-center">
					<div
						role="button"
						tabIndex={0}
						style={{ cursor: 'pointer' }}
						onClick={() => setVisible(true)}
						onKeyDown={() => setVisible(true)}
						className={`ml-3 ${classes.burgeri}`}
					>
						<img alt="burger" className={classes.burger} src={burger} />
					</div>

					<Platform />
					<BlogDropdown />
					<Link className={`${classes.navItems} ant-dropdown-link ml-3`} to="/shop">
						فروشگاه
					</Link>
					<Link className={`${classes.navItems} ant-dropdown-link ml-3`} to="/category/items">
						لوازم جانبی
					</Link>
					<Link className={`${classes.navItems} ant-dropdown-link ml-3`} to="/category/console">
						کنسول گیمینگ
					</Link>
				</div>

				<div className="d-flex align-items-center mr-3">
					<InputMedia drawer={false} media="min" />
					<Link to="/">
						<img alt="logo" className={classes.warzone} src={warzone} />
						<img alt="logo" className={classes.logo} src={cod} />
					</Link>
				</div>
			</div>
		</React.Fragment>
	);
}

export default Nav;
