import React from 'react';
import InputMedia from '../InputMedia/InputMedia';
import classes from './DrawerComp.module.scss';

// Components
import DrawerItems from '../DrawerItems/DrawerItems';
import ItemsP from '../ItemsP/ItemsP';

// Images
import cart from '../../img/cart.png';
import news from '../../img/news.png';
import other from '../../img/other.png';
import game from '../../img/game.png';
import ps4 from '../../img/ps4.png';
import xbox from '../../img/xbox.png';
import pc from '../../img/pc.png';
import mobile from '../../img/mobile.png';
import tech from '../../img/tech.png';
import headset from '../../img/headset.png';
import console from '../../img/console.png';

function DrawerComp({ close }) {
	return (
		<div className="d-flex flex-column justify-content-start align-items-start">
			<InputMedia drawer={true} clicked={close} media="max" cls="mb-3 ml-3 mt-3" />
			<span className="w-100" onClick={close} onKeyDown={close} tabIndex={0} role="button">
				<DrawerItems link="/shop">
					<img src={cart} alt="shop" className={classes.icons} />
					<ItemsP>فروشگاه</ItemsP>
				</DrawerItems>
				<DrawerItems link="/category/console">
					<img src={console} alt="shop" className={classes.icons} />
					<ItemsP>کنسول گیمینگ</ItemsP>
				</DrawerItems>
				<DrawerItems link="/category/items">
					<img src={headset} alt="shop" className={classes.icons} />
					<ItemsP>لوازم جانبی</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog">
					<img src={news} alt="shop" className={classes.icons} />
					<ItemsP>آخرین مطالب</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/game">
					<img src={game} alt="shop" className={classes.icons} />
					<ItemsP>گیم های جدید</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/ps4">
					<img src={ps4} alt="shop" className={classes.icons} />
					<ItemsP>PS4</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/xbox">
					<img src={xbox} alt="shop" className={classes.icons} />
					<ItemsP>XBOX</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/pc">
					<img src={pc} alt="shop" className={classes.icons} />
					<ItemsP>PC</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/mobile">
					<img src={mobile} alt="shop" className={classes.icons} />
					<ItemsP>گوشی موبایل</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/tech">
					<img src={tech} alt="shop" className={classes.icons} />
					<ItemsP>تکنولوژی</ItemsP>
				</DrawerItems>
				<DrawerItems link="/blog/other">
					<img src={other} alt="shop" className={classes.icons} />
					<ItemsP>سایر مطالب</ItemsP>
				</DrawerItems>
			</span>
		</div>
	);
}

export default DrawerComp;
