import React from 'react';
import { Menu, Dropdown } from 'antd';
import { DownOutlined } from '@ant-design/icons';
import { Link } from 'gatsby';
import classes from '../Nav/Nav.module.scss';

const platform = (
	<Menu>
		<Menu.Item>
			<Link to="/blog/ps4" className={`${classes.navItems} p-3 text-center`}>
				PS4
			</Link>
		</Menu.Item>
		<Menu.Item>
			<Link to="/blog/xbox" className={`${classes.navItems} p-3 text-center`}>
				XBOX
			</Link>
		</Menu.Item>
		<Menu.Item>
			<Link to="/blog/pc" className={`${classes.navItems} p-3 text-center`}>
				PC
			</Link>
		</Menu.Item>
	</Menu>
);

function Platform() {
	return (
		<Dropdown className="ml-4" placement="bottomCenter" trigger={[ 'click' ]} overlay={platform}>
			<span className={`${classes.navItems} ant-dropdown-link`}>
				پلتفرم <DownOutlined />
			</span>
		</Dropdown>
	);
}

export default Platform;
