import { navigate } from 'gatsby';
import React from 'react';

function DrawerItems({ children, link }) {
	const [ hover, setHover ] = React.useState(false);

	const hoverhandle = () => {
		setHover(true);
	};
	const unhover = () => {
		setHover(false);
	};

	return (
		<div
			role="button"
			onMouseOver={hoverhandle}
			onMouseLeave={unhover}
			onFocus={hoverhandle}
			tabIndex={0}
			style={{
				background: hover ? '#ECECEC  ' : 'transparent',
				transition: 'all .4s',
				width: '100%',
				cursor: 'pointer'
			}}
			className="d-flex justify-content-start align-items-center pt-3 pb-3"
			onClick={() => navigate(link)}
			onKeyDown={() => navigate(link)}
		>
			{children}
		</div>
	);
}

export default DrawerItems;
